  padding-right: 40px;
  transition: all .3s; }
  .btn:hover, .btn:focus {
    color: var(--color-white);
    background: shade(#29f29f, 10%);
    text-decoration: none;
    outline: none;
    background-color: #BC947B; }
  .btn[disabled],
  [disabled] .btn {
    cursor: not-allowed; }
  .btn--white {
    color: #fff; }
  .btn--primary {
    background: #29f29f;
    color: #2B2E31; }
    .btn--primary:hover, .btn--primary:focus {
      border-color: shade(#29f29f, 10%);
      background: shade(#29f29f, 10%); }
  .btn--secondary {
