  height: 2px;
  width: 24px;
  border-radius: 3px;
  background-color: #000;
  transition: transform 0.6s cubic-bezier(0.68, -0.55, 0.265, 1.55), width 0.6s cubic-bezier(0.68, -0.55, 0.265, 1.55), opacity 0.6s ease, -webkit-transform 0.6s cubic-bezier(0.68, -0.55, 0.265, 1.55); }
  .site-nav-open__icon-inner:before, .site-nav-open__icon-inner:after {
    content: '';
    display: block;
    top: 7px;
